@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  font-family: 'Montserrat', sans-serif;
}
.App {
  text-align: center;
  background-color: #60605f;
  color: white;
  height: 100vh;
  display:flex;
  flex-direction: column;
  
}

.centerConversor{
  padding: 0;
  margin: 0 auto;
  height: 100%;
  width: 75%;
  display: flex;
  align-items: center;
}

h1{
  color: #fff;
  font-size: 3rem;
}

.centerFooter{
  padding: 0;
  margin: 0 auto;
  width: 75%;
}


*{
    margin: 0;
    padding: 0;
}

.options{
    margin: auto;
    width: 375px;
    max-height: 300px;
    overflow: auto;
    display: none;
    z-index: 200;
    color: #fff;
    background-color: #000;
    border-radius: 10px;
    position: absolute;
    box-shadow: 0 0 5px 5px rgba(0,0,0,.3);
}

.options.active{
    display: block;
}

.opt{
    cursor: pointer;
    padding: 15px 0px 0px 15px;
}

.opt input{
    text-align: center;
    padding-bottom: 15px;
    background: none;
    color: white;
    border: none;
    outline: none;
    cursor: pointer;
    font-size: 0.9em;
    font-weight: bold;
    width: 100%;
}

.opt:hover{
    background-color: lightgrey;
}
.opt:hover input{
    color: #000;
}
.class{
    width: 100%;
}

.selectbox{
    width: 100%;
    height: 4rem;
    background-color: #fdc200;
    border-radius: 50px;
    font-size: 1rem;
    color: #000;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;z-index: 500;
}

/* Works on Firefox */
.options{
    scrollbar-width: thin;
    scrollbar-color: #000 #fdc200;
  }
  
  /* Works on Chrome, Edge, and Safari */
  .options::-webkit-scrollbar {
    width: 12px;
  }
  
  .options::-webkit-scrollbar-track {
    background: #fdc200;
    border-radius: 15px;
  }
  
  .options::-webkit-scrollbar-thumb {
    background-color: #000;
    border-radius: 20px;
    border: 3px solid #fdc200;
  }
.container{
    margin: 0 auto;
    padding: 1rem 2rem;
    border-radius: 75px;
    background-color: transparent;
    border: 2px solid #fff;
    box-shadow: 0 0 20px rgba(0,0,0,.5);
}

.row{
    display: flex;
    flex-direction: row;
}

.btn{
    border: none;
    margin: 0 15px;
    width: 143px;
    cursor:pointer;
    border-radius: 50%;
}

.mouseHover:hover{
    box-shadow: 0 0 15px #fdc200;
}

.double-arrow{
    background: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: cover;
}

.arrow{
    background: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-size: cover;
}

.box{
    width: 100%;
    height: 4rem;
    background-color: #fdc200;
    border-radius: 50px;
    text-align-last: center;
    font-size: 25px;
    color: #000;
    font-weight: bold;
    outline: none;
    border: none;
}

button:focus,select:focus, input:focus{
   outline: none;
   border: none;
}
*{
    margin:0;
    padding:0;
}

.row{
    display: flex;
    flex-direction: row;
}

.logo{
    padding: 1.5rem 0 0 1.5rem;
   width: 150px;
}
*{
    margin: 0;
    padding: 0;
}


.footer {
    border: 2px solid #fff;
    border-radius: 20px 20px 0 0;
}
.footer:hover, .footer:hover a {
    background-color: #fdc200;
    color: #000;
    border-color:  #fdc200;
    box-shadow: 0 0 5px 5px rgba(253,194,0,.7);
}
.footer a{
    color: #fff;
    text-decoration: none;
    font-family: 'Roboto', sans-serif !important;
}
